.page {
  font-family: "Space Grotesk", sans-serif;
  font-size: 16px;
  line-height: 140%;
  color: $main-text-color;
  max-width: 100%;
  overflow-x: hidden;
  scroll-behavior: smooth;

  &__button {
    width: 100%;
  }

  &__body {
    margin: 0;
    min-width: 320px;
    max-width: 100%;
    overflow-x: hidden;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    opacity: 0;
    transition: all $effectduration;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
}
