.button {
  border-radius: 8px;
  background-color: $main-text-color;
  color: white;
  height: 56px;
  border: none;
  font-family: "Space Grotesk", sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 27px;
  letter-spacing: 0;
  text-align: center;
  transition: $effectduration;
  width: 100%;

  @include on-desktop {
    width: 324px;
  }

  &:hover {
    border: 2px solid #f0f1f4;
    background-color: #f8f8fa;
    color: $main-text-color;
    cursor: pointer;
  }

  &:active {
    border: none;
    color: white;
    background-color: #111;
  }

  &--always-big {
    width: 100%;
  }

  &--send {
    background-color: $input-background-color;
    color: $main-text-color;
  }
}
